html {
  font-size: 18px;
}

@font-face {
  font-family: 'PP object sans ';
  src: url('/public/Assets/object.ttf') format('truetype');
  font-style: normal;
}

.mySwiper {
  width: 60%;
  height: 100%;
  margin-top: 50px;
}

.slide {
  cursor: pointer;
}

.swiper-container {
  display: flex;
  overflow: hidden;
}

.swiper-wrapper {
  width: 100%;
}

.swiper-slide {
  width: 545px !important;
  margin-right: 20px;
}

.photo-div {
  overflow: hidden;
}

.photo {
  vertical-align: middle;
  aspect-ratio: 3/2;
  object-fit: cover;
  width: 530px !important;
  transition: transform 0.7s ease-in-out;
}

.photo:hover {
  transform: scale(1.2);
  transition-timing-function: cubic-bezier(.4, 0, 0.2, 1);
}

.Name-Date {
  display: flex;
  justify-content: space-between;
  margin-top: 1rem;
  width: 100%;
  height: 100%;
  font-family: "PT Sans", serif;
  font-weight: 400;
  font-style: normal;
  font-size: 1.25rem;
  line-height: 1rem;
}

.author {
  color: #0c47c7;
  font-weight: 400;
  text-transform: uppercase;
}

.body-text {
  font-size: 1.75rem;
  line-height: 2.5rem;
  width: 70%;
  color: black;
  display: flex;
  justify-content: start;
  font-family: 'PP object sans', sans-serif;
  font-weight: 300;
}

.buttons {
  position: absolute;
  right: 10%;
  top: 18%;
  display: flex;
  flex-direction: row;
}

.forward {
  border: 1.5px solid black;
  border-radius: 50px;
  width: 60px;
  height: 60px;
  background: none;
  margin-left: 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.forward:hover {
  background-color: #0c47c7;
  color: white;
  border: #0c47c7;
}

.back {
  border: 1.5px solid black;
  border-radius: 50px;
  width: 60px;
  height: 60px;
  background: none;
  margin-left: 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.back:hover {
  background-color: #0c47c7;
  color: white;
  border: #0c47c7;
}

.arrow-back {
  font-size: 30px;
  font-weight: 700;

}
.arrow-forward {
  font-size: 30px;
  font-weight: 700;
  transform: rotate(180deg);
  position: absolute;
  top: 17px;
}